.ana-main {
    margin-top: 6em;
    text-align: center;
    font-size: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.ana-btn {
    font-family: Poppins-Medium;
    background: yellow;
    color: darkblue;
    font-weight: bold;
    border: 3px solid yellow;
    padding: 20px 80px;
    width: 300px;
    margin-bottom: 20px;
    border-radius: 10px;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
}

.ana-btn:hover {
    background: darkblue;
    color: yellow;
    border: 3px solid darkblue;
    cursor: pointer;
}

.ana-btn-clicked {
    font-family: Poppins-Medium;
    background: yellow;
    color: darkblue;
    font-weight: bold;
    border: 3px solid yellow;
    padding: 20px 80px;
    width: 80%;
    margin-bottom: 20px;
    border-radius: 10px;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
}

.ana-bck {
    color: yellow;
}

.ana-data {
    font-family: Poppins-Regular;
    padding: 20px 80px;
    background-color: white;
    color: black;
    width: 80%;
    border-radius: 10px;
}


